import React from 'react';
import LandingPage from "./LandingPage/LandingPage";
import {HashRouter as Router, Route, Switch,} from "react-router-dom";
import Header from "./Header/Header";
import Footer from "./Footer/Footer";
import Order from "./OrderPage/Order";
import Payment from "./PaymentPage/Payment";
import Confirmation from "./OrderConfirmationPage/Confirmation";

function App() {
    return (
        <Router basename={"/funcbox"} getUserConfirmation={((message, callback) => {callback(true)})}>
            <Header/>
            <Switch>
                <Route exact path={"/order"}><Order/></Route>
                <Route exact path={"/pay"}><Payment/></Route>
                <Route exact path={"/done"}><Confirmation/></Route>
                <Route exact path={"/"}><LandingPage/></Route>
            </Switch>
            <Footer/>
        </Router>
    );
}

export default App;