import "./footer.css"
import "../index.css"

function Footer(){
    return <div className={"footer_holder"}>
        <div className={"footer_input_holder"}>
            <label htmlFor={"touch"} className={"global_normal_text global_normal_big_text"}>Get in Touch</label>
            <input id={"touch"} className={"footer_input global_normal_text global_normal_big_text"}/>
        </div>
        <div className={"footer_links global_normal_text"}>
            <div>
                <p>Join our Team</p>
                <p>Terms</p>
                <p>Privacy</p>
            </div>
        </div>
        <div className={"footer_made_with"}>
            Made with <img src={"images/heart.png"} alt={"love"}/> by Team FuncBox
        </div>
        <div className={"footer_social"}>
            <img src={"images/in-icon.png"} alt={"linkedin"}/>
            <img src={"images/fb-icon.png"} alt={"facebook"}/>
            <img src={"images/ig-icon.png"} alt={"instagram"}/>
        </div>
    </div>
}

export default Footer