import "./p3.css"

function FaqItem() {
    return (
        <div className={"p3_faq_item"}>
            <div className={"p3_faq_question global_normal_text"}>What age is Funcbox for?</div>
            <div className={"p3_faq_answer global_normal_text"}>Funcbox is designed for kids 6-12 years in age.</div>
        </div>
    )
}

function P4() {
    return <>
        <div className={"p3_title global_normal_text"}>Frequently Asked Question</div>
        <div className={"p3_faq_holder"}>
            {
                [1, 2, 3].map((i: number) => {
                    return <FaqItem key={i}/>
                })
            }
        </div>
    </>
}

export default P4